import logo from './logo.svg';
import './App.css';
import { useState, useEffect } from 'react';
import Chart from "react-apexcharts";
import Heatmap from './Heatmap';

async function getWeatherData(station, metric, setWeatherData) {
  if (station && metric) {
    // const response = await fetch(`http://localhost:5000/weather_data/${station}/1d/${metric}.json`);
    const response = await fetch(`https://weather.sidverma.io/data/weather_data/${station}/1d/${metric}.json`);
    const data = await response.json();
    setWeatherData(data);
  }
}

