function App() {
  const [stations, setStations] = useState([]);
  const [metrics, setMetrics] = useState([]);
  const [stats, setStats] = useState([]);
  const [selectedStation, setSelectedStation] = useState('');
  const [selectedMetric, setSelectedMetric] = useState('');
  const [selectedStat, setSelectedStat] = useState('');
  const [weatherData, setWeatherData] = useState({});
  const [chartSeries, setChartSeries] = useState([]);
  const [yearLabels, setYearLabels] = useState([]);

  useEffect(() => {
    // fetch('http://localhost:5000/index.json')
    fetch('https://weather.sidverma.io/data/index.json')
      .then(response => response.json())
      .then(data => {
        setStations(data.stations)
        setMetrics(data.observations)
        setStats(data.transformers)
      })
      .catch(error => console.error(error))
  }, []);

  // useEffect(() => {
  //   if (!selectedStation) {
  //     setSelectedStation("VOGO_9_IN")
  //   }
  //   getWeatherData(selectedStation, selectedMetric, setWeatherData);
  // }, [stations, selectedStation]);

  useEffect(() => {
    if (!selectedStation) {
      setSelectedStation("VOGO_9_IN")
    }
    if (!selectedMetric) {
      setSelectedMetric("temp")
    }
    if (!selectedStat) {
      setSelectedStat("avg")
    }
    getWeatherData(selectedStation, selectedMetric, setWeatherData);
    // setChartData(weatherData, selectedStation, selectedMetric, selectedStat, setChartSeries, setYearLabels);

  }, [selectedStation, selectedMetric, selectedStat]);

  return (
    <div className="App">
    <select value={selectedStation} onChange={event => setSelectedStation(event.target.value)}>
      {
      stations.map(city => (
        <option key={city} value={city}>
          {city}
        </option>
      ))
      }
    </select>

    <select  value={selectedMetric} onChange={event => setSelectedMetric(event.target.value)}>
      {
      metrics.map(metric => (
        <option key={metric} value={metric}>
          {metric}
        </option>
      ))
      }
    </select>

    <select  value={selectedStat} onChange={event => setSelectedStat(event.target.value)}>
      {
      stats.map(stat => (
        <option key={stat} value={stat}>
          {stat}
        </option>
      ))
      }
    </select>

      {weatherData && weatherData.values && (
        <Heatmap
          xAxis = {weatherData.x_axis}
          yAxis = {weatherData.y_axis}
          values = {weatherData.values[selectedStat]}
        />
      )}
    </div>
  );
}

export default App;
