import * as d3 from "d3";
import { useEffect, useRef } from "react";

function Heatmap({xAxis, yAxis, values}) {
  console.log(xAxis, yAxis, values)
  const ref = useRef();
  useEffect(() => {
    // empty the ref element
    d3.select(ref.current)
    .selectChildren()
    .remove()
    // append the svg object to the ref element
    // Declare the chart dimensions and margins.
    const marginTop = 20;
    const marginRight = 1;
    const marginBottom = 40;
    const marginLeft = 40;
    const rowHeight = 3;
    const width = 928;
    const height = rowHeight * yAxis.length + marginTop + marginBottom;

    // Create the SVG container.
    const svg = d3
    .select(ref.current).append("svg")
        .attr("viewBox", [0, 0, width, height])
        .attr("viewBox", [0, 0, width, height])
        .attr("width", width)
        .attr("height", height)
        .attr("style", "max-width: 100%; height: auto;");

    // Create the scales.
    const x = d3.scaleLinear()
      .domain([d3.min(xAxis), d3.max(xAxis) + 1])
      .rangeRound([marginLeft, width - marginRight])

    const y = d3.scaleBand()
      .domain(yAxis)
      .rangeRound([marginTop, height - marginBottom])

    const color = d3.scaleSequentialSqrt([d3.min(values, d => d3.min(d)), d3.max(values, d => d3.max(d))], d3.interpolateOrRd);

    // Append the axes.
    svg.append("g")
        .call(g => g.append("g")
          .attr("transform", `translate(0,${marginTop})`)
          .call(d3.axisTop(x).ticks(null, "d"))
          .call(g => g.select(".domain").remove()))
        .call(g => g.append("g")
          .attr("transform", `translate(0,${height - marginBottom + 4})`)
          .call(d3.axisBottom(x)
              .tickValues([xAxis])
              .tickFormat(x => x)
              .tickSize(marginTop + marginBottom - height - 10))
          .call(g => g.select(".tick text")
              .clone()
              .attr("dy", "2em")
              .style("font-weight", "bold")
              .text("Measles vaccine introduced"))
          .call(g => g.select(".domain").remove()));

    svg.append("g")
        .attr("transform", `translate(${marginLeft},0)`)
        .call(d3.axisLeft(y).tickSize(0))
        .call(g => g.select(".domain").remove());

    // Create a cell for each (state, year) value.

    const format = d => d == null ? "N/A"
        : `${d3.format(".1f")(d)}`;

    svg.append("g")
      .selectAll("g")
      .data(values)
      .join("g")
      .attr("transform", (d, i) => `translate(0,${y(yAxis[i])})`)
    .selectAll("rect")
    .data((d, i) => d.map((value, j) => ({value, yValue: yAxis[i], xValue: xAxis[j]})))
    .join("rect")
      .attr("x", (d, i) => x(d.xValue) + 1)
      .attr("width", (d, i) => x(d.xValue + 1) - x(d.xValue))
      .attr("height", y.bandwidth())
      .attr("fill", d => isNaN(d.value) ? "#eee" : d.value === 0 ? "#fff" : color(d.value))
    .append("title")
      .text(d => `${format(d.value)} ${d.xValue} ${d.yValue}`);

  }, [xAxis, yAxis, values]);

    return  <div ref={ref} />
};

export default Heatmap;